	import {queryCouponCanUsed,queryCouponCantUsed,queryCouponHasUsed} from '@/common/api.js'
	import couponConfig from '@/common/coupon-config.js'
	
    export default {
        onLoad(options) {
            this.color = this.BASECOLOR;
			this.grpId=options.grpId;
			this.queryCoupon();
			this.ossDownloadUrl = this.HTTPCONFIG.ossDownloadUrl;
			
        },

        data() {
            return {
				ossDownloadUrl:'',
				grpId:'',
                color: '',
                TabCur: 0,
                scrollLeft: 0,
                tapTopContent: ['我的券','第三方券' ,'已使用券','不可用券'],
				used:[],//正常可以使用
				disabled:[],//过期，禁用，失效
				third:[],//第三方机构券
				hasUsed:[],//已核销
				currentPage:[1,1,1,1],
				hasLoadAll:[false,false,false,false],

            }
        },
        methods: {
            tabSelect(e) {
                this.TabCur = e.currentTarget.dataset.id;
                this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
				// this.queryCoupon();
            },
            tapBodyScroll(e) {
                this.TabCur = e.detail.current;
                this.scrollLeft = (e.detail.current - 1) * 60
				this.queryCoupon();
            },
			toMycard(item){
				this.COMMONFUNCTION.toPage('/pages/mycoupon/mycoupon?couponCode='+item.couponCode+'&couponId='+item.couponId)
			},
				
			queryCoupon(){
				if(this.hasLoadAll[this.TabCur])
					return;
				if(this.TabCur==0){
					queryCouponCanUsed(this.currentPage[this.TabCur],20,1,'','','',true).then((res)=>{
						this.currentPage[this.TabCur]++;
						this.used=this.used.concat(res.data);
						if(this.used.length>=res.total)
							this.hasLoadAll[this.TabCur]=true;
					}).catch(e=>{
						uni.stopPullDownRefresh();
					})
				}else if(this.TabCur==1){
					queryCouponCanUsed(this.currentPage[this.TabCur],20,2,'','','',true).then((res)=>{
						this.currentPage[this.TabCur]++;
						this.third=this.third.concat(res.data);
						if(this.third.length>=res.total)
							this.hasLoadAll[this.TabCur]=true;
					}).catch(e=>{
						uni.stopPullDownRefresh();
					})
				}else if(this.TabCur==2){
					
					queryCouponHasUsed(this.currentPage[this.TabCur],20,true).then((res)=>{
						this.currentPage[this.TabCur]++;
						this.hasUsed=this.hasUsed.concat(res.data);
						if(this.hasUsed.length>=res.total)
							this.hasLoadAll[this.TabCur]=true;
					}).catch(e=>{
						uni.stopPullDownRefresh();
					})
				}else if(this.TabCur==3){
					
					queryCouponCantUsed(this.currentPage[this.TabCur],20,true).then((res)=>{
						this.currentPage[this.TabCur]++;
						this.disabled=this.disabled.concat(res.data);
						if(this.disabled.length>=res.total)
							this.hasLoadAll[this.TabCur]=true;
					}).catch(e=>{
						uni.stopPullDownRefresh();
					})
				}	
			}
        },
		onPullDownRefresh() {
			this.currentPage[this.TabCur]=1;
			this.queryCoupon();
		},
    }
